import "../styles/App.css";
import { useEffect } from "react";

function Contact() {
  // window.scrollTo(0, 0);

  const ContactInit = () => {
    //
    //

    const MapsInit = document.createElement("script");

    MapsInit.src = "https://alpha.vdfm.ga/map.js";
    MapsInit.async = true;

    document.body.appendChild(MapsInit);
    //
    //
  };

  useEffect(ContactInit, []);
  return (
    <main className="Contact">
      <section className="Section" id="Map_Header">
        <div id="VDFM_map"></div>
        {/* <script
          src="https://maps.googleapis.com/maps/api/js?key=AIzaSyDI1Bt5fcHnAeU5hc1Q6yswi6qwe3N0P3w&callback=initMap&v=weekly"
          async
        ></script>
        <script src="https://alpha.vdfm.ga/map.js" async></script> */}
      </section>
      <section className="Section" id="sec2"></section>
      <section className="Section" id="sec3"></section>
      <section className="Section" id="sec4"></section>
      <section className="Section" id="sec5"></section>
    </main>
  );
}
export default Contact;
